const SEPARATE = ', ';

export const ANDROID = '.apk, .aia';

export const CSV = '.csv, .tsv';

export const DOCS = '.pdf, .doc, .docx, .xls, .xlsx, .ppt, .pptx, .odt, .ods, .odp';

export const IMAGES = '.png, .jpeg, .jpg, .gif, .svg, .webp';

export const OTHER = '.cs, .unitypackage, .ipynb, .rbxl';

export const PYTHON = '.py';

export const SCRATCH2 = '.sb2';

export const SOUND = '.mp3, .ogg, .wav';

export const SQL = '.sqlite';

export const TEXT = '.txt';

export const VIDEO = '.mp4';

export const WEB = '.html, .css, .js, .json, .xml';

export const ANY = [ANDROID, CSV, DOCS, IMAGES, OTHER, PYTHON, SCRATCH2, SOUND, SQL, TEXT, VIDEO, WEB].join(SEPARATE);

export const LABORATORY = [IMAGES, TEXT, SCRATCH2, PYTHON, DOCS, ANDROID].join(SEPARATE);

export const LABORATORY_PREVIEW = '.png, .jpeg, .jpg, .webp';

export const PYTHON_LEVEL = [IMAGES, SOUND].join(SEPARATE);

export const VSCODE = [CSV, DOCS, IMAGES, OTHER, PYTHON, SCRATCH2, SOUND, SQL, TEXT, VIDEO, WEB].join(SEPARATE);
