/**
 * This file contains actions that will be send from vscode to platform.
 */

import { ActionType, createStandardAction } from 'typesafe-actions';
import { IBaseFileInfo, IError, IProgressState } from '../types';

const error = createStandardAction('vscode-integration/VSCODE/ERROR')<IError>();

const progress = createStandardAction('vscode-integration/VSCODE/PROGRESS')<IProgressState>();

const stopProject = createStandardAction('vscode-integration/VSCODE/STOP_PROJECT')();

const fileChange = createStandardAction('vscode-integration/VSCODE/FILE_CHANGE')<{
  file: {
    content: string;
    fileName: string;
  };
  isDeleted: boolean;
  isNew: boolean;
}>();

const saveLocallyRequest = createStandardAction('vscode-integration/VSCODE/SAVE_LOCALLY_REQUEST')();

const levelState = createStandardAction('vscode-integration/VSCODE/LEVEL_STATE')<{
  files: IBaseFileInfo[];
}>();

const execCommand = createStandardAction('vscode-integration/VSCODE/EXEC_COMMAND')<string>();

export const VscodeActions = {
  error,
  execCommand,
  fileChange,
  levelState,
  progress,
  saveLocallyRequest,
  stopProject,
};

export type VscodeAction = ActionType<typeof VscodeActions>;
