export const UNIVERSAL_PATH_SEP = '/';
export const WINDOWS_PATH_SEP = '\\';
export const REGEXP_PATH_SEP = /[\\\/]/g;

export function convertPathToUnix(path: string) {
  return path.replace(REGEXP_PATH_SEP, UNIVERSAL_PATH_SEP);
}

export function convertPathToWindows(path: string) {
  return path.replace(REGEXP_PATH_SEP, WINDOWS_PATH_SEP);
}
