export enum ExtensionAction {
  SAVE = 'save',
  LOAD = 'load',
  UPLOAD = 'upload',
  LIST = 'list',
}

export enum ErrorCode {
  FILE_ALREADY_EXISTS = 'file_already_exists',
}

export interface IError {
  message?: string;
  exception?: Error;
  failedAction?: ExtensionAction;
  code?: ErrorCode;
}

export interface IProgressState {
  complete: boolean;
  message?: string;
  action?: ExtensionAction;
}

export interface IEncodedBase64File {
  fileName: string;
  content: string;
}

export interface IBaseFileInfo {
  filePath: string;
  updatedAt: number;
}

export interface IFilePathDelete {
  filePath: string;
}

export interface ISyncData {
  level: {
    config: {
      files: {
        defaultCode: IBaseFileInfo | null;
        materials: IBaseFileInfo[];
      };
    };
  };
  materials: IEncodedBase64File[];
  projectFiles?: IEncodedBase64File[];
}
