/**
 * This file contains actions that will be send from platform to vscode.
 */

import { ActionType, createStandardAction } from 'typesafe-actions';
import { IEncodedBase64File, ISyncData } from '../types';

const uploadFiles = createStandardAction('vscode-integration/PLATFORM/UPLOAD_FILES')<{
  files: IEncodedBase64File[];
  overwrite: boolean;
}>();

const stopProject = createStandardAction('vscode-integration/PLATFORM/STOP_PROJECT')();

const updateLocale = createStandardAction('vscode-integration/PLATFORM/UPDATE_LOCALE')<{ locale: string }>();

const runProject = createStandardAction('vscode-integration/PLATFORM/RUN_PROJECT')<{
  files: IEncodedBase64File[];
  projectId: number;
}>();

const dispose = createStandardAction('vscode-integration/PLATFORM/DISPOSE')();

const reload = createStandardAction('vscode-integration/PLATFORM/RELOAD')();

const saveRequest = createStandardAction('vscode-integration/PLATFORM/SAVE_REQUEST')();

const levelInit = createStandardAction('vscode-integration/PLATFORM/LEVEL_INIT')<{
  isBackofficePreview: boolean;
  isProjectOutdated: boolean;
  levelId: number;
  levelTitle: string;
  levelType: string;
  projectSlug: string;
  studentId: number;
}>();

const syncFiles = createStandardAction('vscode-integration/PLATFORM/SYNC_FILES')<ISyncData>();

const keyDown = createStandardAction('vscode-integration/PLATFORM/KEY_DOWN')<Partial<KeyboardEvent>>();

const closeAllEditors = createStandardAction('vscode-integration/PLATFORM/CLOSE_ALL_EDITORS')();

const locationChange = createStandardAction('vscode-integration/PLATFORM/LOCATION_CHANGE')<{ href: string }>();

const updateHost = createStandardAction('vscode-integration/PLATFORM/UPDATE_HOST')<{ host: string }>();

export const VscodePlatformActions = {
  closeAllEditors,
  dispose,
  keyDown,
  levelInit,
  locationChange,
  reload,
  runProject,
  saveRequest,
  stopProject,
  syncFiles,
  updateHost,
  updateLocale,
  uploadFiles,
};

export type VscodePlatformAction = ActionType<typeof VscodePlatformActions>;
