import { Agent } from 'https';
import { IntlMessageFormat, PrimitiveType } from 'intl-messageformat';
import fetch, { RequestInit } from 'node-fetch';

import { HOST, PROTOCOL } from './env';
import { IMessagesResponseJson, Translations } from './types';

const DEFAULT_LOCALE = 'en-us';
const CATEGORY = 'algo/vscode';

let translations: Translations = {};
let locale = DEFAULT_LOCALE;

const getMessage = (id: string) => {
  if (!translations[locale]) return id;
  const messages = translations[locale][CATEGORY] || {};
  return messages[id] || id;
};

const getDefaultLocale = () => {
  const config = process.env.VSCODE_NLS_CONFIG || `{ "locale": "${DEFAULT_LOCALE}" }`;
  let lang: string = JSON.parse(config).locale;
  if (!lang.includes('-')) {
    lang = [lang, lang].join('-');
  }
  console.log('lang', lang);
  const msg = new IntlMessageFormat('', lang);
  lang = msg.resolvedOptions().locale;
  console.log('lang normalized', lang);
  return lang;
};

export const getLocale = () => locale;

export const localize = (id: string, values?: Record<string, PrimitiveType>) => {
  const msg = new IntlMessageFormat(getMessage(id), locale);
  return msg.format(values);
};

const setTranslations = (localeName: string, data: Translations) => {
  translations = data;
  locale = localeName;
};

export const requestMessages = async (lang?: string) => {
  if (!lang) {
    lang = getDefaultLocale();
  }

  const url = `${PROTOCOL}//${HOST}/api/v1/messages/index?categories[]=${CATEGORY}`;

  const config: RequestInit = {
    headers: {
      'Accept-Language': [lang, lang.split('-')[0]].join(','),
    },
  };

  config.agent = new Agent({
    rejectUnauthorized: false,
  });

  return fetch(url, config)
    .then(response => response.json())
    .then(json => {
      const data = (json as IMessagesResponseJson).data;
      console.log('set new locale', data.locale);
      setTranslations(data.locale, data.messages);
    });
};
